body {
    background-color: #1F2833;
    margin: 0px;
}

.container {
    background-color: rgb(130, 149, 155);
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    box-shadow: 0 15px 50px rgba(0, 0, 0, 0.12), 0 50px 45px rgba(0, 0, 0, 0.24);
    border-radius: 10%;
    width: 40%;
}

